#cloud {
	position: absolute;
	top: 200px;
	left: 200px;
	visibility: hidden;
}

#cloud-puff-1 {
	height: 7vh;
	width: 7.5vw;
	background: linear-gradient(rgba(255, 255, 255, 1), rgb(201, 201, 201));
	border-bottom-right-radius: 30%;
	border-bottom-left-radius: 20%;
	border-top-right-radius: 40%;
	border-top-left-radius: 50%;
}
  
#cloud-puff-2 {
	height: 4.5vh;
	width: 5vw;
	background: linear-gradient(rgba(255, 255, 255, 1), rgb(182, 182, 182));
	border-radius: 50%;
	position: absolute;
	top: 25px;
	left: 25px;
}
  
#cloud-puff-3 {
	height: 2.9vh;
	width: 3vw;
	background: linear-gradient(rgba(255, 255, 255, 1), rgba(201, 201, 201, 1));
	border-radius: 50%;
	position: absolute;
	top: 1px;
	left: 45px;
}
  
#cloud-puff-4 {
	height: 3vh;
	width: 4vw;
	background: linear-gradient(rgba(255, 255, 255, 1), rgba(201, 201, 201, 1));
	border-radius: 50%;
	position: absolute;
	top: 40px;
	right: 39px;
}